import React, { useState } from 'react'
import Navbar from './Menubar/Navbar';
import Sidebar from './Sidebar/Sidebar';

function Menu() {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    };

    return (
        <>
         <Sidebar isOpen={isOpen} toggle={toggle} />
         <Navbar toggle={toggle} />   
        </>
    )
}

export default Menu
